import {
  JWT_EXPIRED_MSG
} from "@/common/constant";

function rand(min, max) {
  return min + Math.floor(Math.random() * (max - min + 1));
}

function testJWTExpiredMsg(msg) {
  let regex = /^JWT expired.*$/;
  return regex.test(msg);
}

function checkJWTExpired(err, that) {
  let msg = "";
  if (testJWTExpiredMsg(err.response.data.message)) {
    msg = JWT_EXPIRED_MSG;
    that.$store.dispatch("auth/purgeAuth");
    console.log("that.$route.path", that.$route.path);
    // https://router.vuejs.org/zh/guide/essentials/navigation.html
    that.$router.push({
      // path: "login",  // Uncaught (in promise) Error: Redirected when going from "/order/20201204212945964LLX" to "/order/login" via a navigation guard
      path: "/login",
      query: {
        redirect: that.$route.path
      },
    });
  } else {
    msg = err.response.data.message;
  }
  return msg;
}

export default {
  rand,
  testJWTExpiredMsg,
  checkJWTExpired
}