import ApiService from "@/common/api.service";
import JwtService from "@/common/jwt.service";

// initial state
const state = () => ({
  user: {}, // {name, token, chName}
  captcha: "",
  uuid: "",
  buyer: {}
  //isAuthenticated: false
})

// getters
const getters = {
  currentUser(state) {
    return state.user;
  },
  isAuthenticated(state) {
    if (Object.keys(state.user).length === 0) {
      return false;
    } else {
      if (state.user.token) {
        return true;
      } else {
        return false;
      }
    }
    // return state.isAuthenticated;
  },
  uuid(state) {
    return state.uuid;
  },
  captcha(state) {
    return state.captcha;
  },
  getBuyer(state) {
    return state.buyer;
  },
}

// mutations
const mutations = {
  refresh(state) {
    state.user = JSON.parse(localStorage.getItem("demo.authentication")) || {};
    state.buyer = JSON.parse(localStorage.getItem("demo.buyer")) || {};
  },
  setAuth(state, auth) {
    // state.isAuthenticated = true;
    state.user = auth;
    localStorage.setItem("demo.authentication", JSON.stringify(state.user));
  },
  clearAuth(state) {
    // state.isAuthenticated = false;
    state.user = {};
    localStorage.removeItem("demo.authentication");
  },
  clearBuyer(state) {
    state.buyer = {};
    localStorage.removeItem("demo.buyer");
  },
  setCaptcha(state, {
    uuid,
    captcha
  }) {
    state.uuid = uuid;
    state.captcha = captcha;
  },
  setBuyer(state, data) {
    state.buyer = JSON.parse(JSON.stringify(data));
    localStorage.setItem("demo.buyer", JSON.stringify(state.buyer));
  },
}

// actions
const actions = {
  refresh({
    commit
  }) {
    commit("refresh");
  },
  getCaptcha({
    commit
  }) {
    return new Promise((resolve, reject) => {
      ApiService.get("captcha/create")
        .then((response) => {
          console.log("getCaptcha response : " + JSON.stringify(response));
          console.log("getCaptcha response.status : " + response.status);
          if (response.data.uuid) {
            commit("setCaptcha", {
              uuid: response.data.uuid,
              captcha: response.data.base64Img
            });
            // resolve(data.message);
            resolve(response.data);
          } else {
            // reject(data.message);
            console.log("captcha/create : reject(response.data)");
            reject(response.data);
          }
        }).catch((err) => {
          console.log("getCaptcha err : " + err);
          if (err.response) {
            console.log("getCaptcha err.response : " + JSON.stringify(err.response));
            // console.log("getCaptcha err.response.status : " + err.response.status);
            reject(err.response.data.message);
          } else {
            reject(err);
          }
        })
    })
  },
  refreshCaptcha({
    commit,
    state
  }) {
    return new Promise((resolve, reject) => {
      ApiService.get("captcha/refresh", state.uuid)
        .then((response) => {
          console.log("refreshCaptcha response : " + JSON.stringify(response));
          console.log("refreshCaptcha response.status : " + response.status);
          if (response.data.uuid) {
            commit("setCaptcha", {
              uuid: response.data.uuid,
              captcha: response.data.base64Img
            });
            resolve(response.data);
          }
        }).catch((err) => {
          console.log("refreshCaptcha err : " + err);
          console.log("refreshCaptcha err.response : " + JSON.stringify(err.response));
          console.log("refreshCaptcha err.response.status : " + err.response.status);
          if (err.response) {
            reject(err.response.data.message);
          } else {
            reject(err);
          }
        })
    })
  },
  login({
    commit,
    state
  }, {
    name,
    password,
    code
  }) {
    return new Promise((resolve, reject) => {
      // if (password == "pwd") {
      // commit("setAuth", {
      // name,
      // token: "eyJhbGciOiJIUzUxMiJ9"
      // });
      // resolve("ok");
      // } else {
      // reject("認證失敗");
      // }

      // https://github.com/axios/axios
      // When using then, you will receive the response as follows:
      // axios.get('/user/12345')
      // .then(function (response) {
      // console.log(response.data);
      // console.log(response.status);
      // console.log(response.statusText);
      // console.log(response.headers);
      // console.log(response.config);
      // });
      ApiService.post("jwt/login", {
          name,
          password,
          uuid: state.uuid,
          code
        })
        // .then(({ data }) => {
        .then((response) => {
          // console.log(JSON.stringify(data));
          console.log("response : " + JSON.stringify(response));
          commit("setAuth", {
            name,
            token: response.data.token,
			chName: response.data.chName
          });
          // resolve(data.message);
          resolve(response.data.message);
        })
        // Axios error.response
        // https://github.com/axios/axios/issues/960
        .catch((err) => {
          console.log("err : " + err);
          console.log("err.response : " + JSON.stringify(err.response));
          if (err.response) {
            // console.log("err.response.data.message : " + JSON.stringify(err.response.data.message));
            // console.log("err.response.data.message : " + err.response.data.message);
            // let jsonObj = JSON.parse(err.response.data.message);
            reject(err.response.data);
          } else {
            reject(err);
          }
        });
    });
  },
  logout({
    commit
  }) {
    commit("clearAuth");
    commit("clearBuyer");
  },
  purgeAuth({
    commit
  }) {
    console.log("purgeAuth");
    commit("clearAuth");
  },
  setBuyer({
    commit,
    state
  }, data) {
    commit("setBuyer", data);
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}