import ApiService from "@/common/api.service";
import util from "@/common/util";

// initial state
const state = () => ({
  items: [],
  book: null,
  newReleases: [],
  weeklyHot: [],
  monthlyHot: [],
  seasonalHot: [],
  yearlyHot: []
})

// getters
const getters = {
  getItems(state) {
    console.log("getItems");
    return state.items;
  },
  getBook(state) {
    console.log("getBook");
    return state.book;
  },
  getNewReleases(state) {
    console.log("getNewReleases");
    return state.newReleases;
  },
  getWeeklyHot(state) {
    console.log("getWeeklyHot");
    return state.weeklyHot;
  },
  getMonthlyHot(state) {
    console.log("getMonthlyHot");
    return state.monthlyHot;
  },
  getSeasonalHot(state) {
    console.log("getSeasonalHot");
    return state.seasonalHot;
  },
  getYearlyHot(state) {
    console.log("getYearlyHot");
    return state.yearlyHot;
  }
}

// mutations
const mutations = {
  refresh(state) {
    state.items = JSON.parse(localStorage.getItem("demo.products.items")) || [];
  },
  changeInventory(state, product) {
    let index = state.items.findIndex((ele) => {
      return ele.isbn === product.isbn;
    });
    if (index != -1) {
      state.items[index].inventory = product.inventory;
      console.log("products.js changeInventory product.isbn", product.isbn);
      // fix [Vue warn]: Error in nextTick: "TypeError: Cannot set property 'inventory' of null"
      if (state.book && state.book.isbn === product.isbn) {
        console.log("products.js changeInventory state.book.isbn", state.book.isbn);
        state.book.inventory = product.inventory;
      }
      localStorage.setItem("demo.products.items", JSON.stringify(state.items));
    }
  },
  setBook(state, product) {
    state.book = product;
  },
  addProduct(state, product) {
    console.log("mutations addProduct");
    let index = state.items.findIndex((ele) => {
      return ele.isbn === product.isbn;
    });
    if (index != -1) {
      //delete old index
      state.items.splice(index, 1);
    }
    // add new
    state.items.push(product);
    localStorage.setItem("demo.products.items", JSON.stringify(state.items));

    //for debug
    // for (let key in state.items) {
    //   console.log("state.items", key, JSON.stringify(state.items[key]));
    // }
  },
  setNewReleases(state, data) {
    console.log("setNewReleases", JSON.stringify(data));
    // https://www.freecodecamp.org/news/how-to-clone-an-array-in-javascript-1d3183468f6a/
    // 8. JSON.parse and JSON.stringify (Deep copy)
    // Note: This one safely copies deeply nested objects/arrays!
    state.newReleases = JSON.parse(JSON.stringify(data));
  },
  setHotProducts(state, {
    weeklyHot,
    monthlyHot,
    seasonalHot,
    yearlyHot
  }) {
    console.log("products.js weeklyHot", JSON.stringify(weeklyHot));
    state.weeklyHot = JSON.parse(JSON.stringify(weeklyHot));
    console.log("products.js monthlyHot", JSON.stringify(monthlyHot));
    state.monthlyHot = JSON.parse(JSON.stringify(monthlyHot));
    console.log("products.js seasonalHot", JSON.stringify(seasonalHot));
    state.seasonalHot = JSON.parse(JSON.stringify(seasonalHot));
    console.log("products.js yearlyHot", JSON.stringify(yearlyHot));
    state.yearlyHot = JSON.parse(JSON.stringify(yearlyHot));
  },

}

// actions
const actions = {
  refresh({
    commit
  }) {
    commit("refresh");
  },
  addProduct({
    commit
  }, product) {
    commit("refresh");
    commit("addProduct", product);
  },
  getProduct({
    commit
  }, isbn) {
    commit("refresh");
    return new Promise((resolve, reject) => {
      ApiService.get("products", isbn)
        .then((response) => {
          console.log("getProduct response : " + JSON.stringify(response));
          console.log("getProduct response.status : " + response.status);
          commit("addProduct", response.data);
          commit("setBook", response.data);
          // resolve(response.data);
          // use deep copy to fix Error: [vuex] do not mutate vuex store state outside mutation handlers.
          resolve(JSON.parse(JSON.stringify(response.data)));
        }).catch((err) => {
          console.log("getProduct err : " + err);
          if (err.response) {
            console.log("getProduct err.response : " + JSON.stringify(err.response));
            console.log("getProduct err.response.status : " + err.response.status);
            reject(err.response.data.message);
          } else {
            reject(err);
          }
        })
    })
  },
  changeInventory({
    commit
  }, product) {
    commit("refresh");
    commit("changeInventory", product);
  },
  getServerProductInventory({
    commit
  }, isbnArray) {
    // commit("refresh");
    return new Promise((resolve, reject) => {
      ApiService.post("products/inventory", {
          isbn: isbnArray
        })
        // .then(({ data }) => {
        .then((response) => {
          // console.log(JSON.stringify(data));
          console.log("response : " + JSON.stringify(response));
          resolve(response.data);
        })
        // Axios error.response
        // https://github.com/axios/axios/issues/960
        .catch((err) => {
          console.log("err : " + err);
          console.log("err.response : " + JSON.stringify(err.response));
          if (err.response) {
            // console.log("err.response.data.message : " + JSON.stringify(err.response.data.message));
            // console.log("err.response.data.message : " + err.response.data.message);
            // let jsonObj = JSON.parse(err.response.data.message);
            reject(err.response.data);
          } else {
            reject(err);
          }
        });
    });
  },
  // async function
  // https://developer.mozilla.org/zh-TW/docs/Web/JavaScript/Reference/Statements/async_function
  // 當 async 函式被呼叫時，它會回傳一個 Promise。
  // 如果該 async 函式回傳了一個值，Promise 的狀態將為一個帶有該回傳值的 resolved。
  // 如果 async 函式拋出例外或某個值，Promise 的狀態將為一個帶有被拋出值的 rejected。
  async getNewBookReleases({
    commit
  }) {
    // await
    // https://developer.mozilla.org/zh-TW/docs/Web/JavaScript/Reference/Operators/await
    // 若將 Promise 物件傳給 await 運算式，它會等待 Promise 解析並回傳 resolve 後的值。
    const newReleases = await ApiService.get("home/newBookReleases");
    console.log("newReleases", JSON.stringify(newReleases));
    commit("setNewReleases", newReleases.data);
    return newReleases.data;
  },
  async getHotSale({
    commit
  }) {
    // await
    // https://developer.mozilla.org/zh-TW/docs/Web/JavaScript/Reference/Operators/await
    // 若將 Promise 物件傳給 await 運算式，它會等待 Promise 解析並回傳 resolve 後的值。
    const hotSale = await ApiService.get("home/booksOrderByPublicationDateDesc");
    console.log("hotSale", JSON.stringify(hotSale));
    let data = hotSale.data;
    let weeklyHot = data.slice(0, 5);
    let monthlyHot = data.slice(5, 10);
    let seasonalHot = data.slice(10, 15);
    // book - 15, 16, 17, util.rand(0, 8), util.rand(8, 14)
    let yearlyHot = JSON.parse(JSON.stringify(data.slice(15, 18)));
    yearlyHot.push(data[util.rand(0, 8)]);
    yearlyHot.push(data[util.rand(8, 14)]);
    commit("setHotProducts", {
      weeklyHot,
      monthlyHot,
      seasonalHot,
      yearlyHot
    });
    return data;
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}