// initial state
const state = () => ({
  items: [],
})

// getters
const getters = {
  cartProducts: (state, getters, rootState) => {
    return state.items;
  },
}

// actions
const actions = {
  refresh({
    commit,
  }) {
    commit("refresh");
  },
  toggleAll({
    commit,
  }, checked) {
    commit("refresh");
    commit("toggleAll", checked);
  },
  setChecked({
    commit,
  }, product) {
    commit("refresh");
    commit("setChecked", product);
  },
  changeQuantity({
    commit,
    state,
    rootState,
  }, product) {
    return new Promise((resolve, reject) => {
      //https://stackoverflow.com/questions/44618440/vuex-how-to-commit-a-global-mutation-in-a-module-action
      commit("products/refresh", null, {
        root: true
      });
      commit("refresh");
      let showMsg = false;
      const cartItem = state.items.find(item => item.isbn === product.isbn);
      if (cartItem) {
        const productItem = rootState.products.items.find(item => item.isbn === product.isbn);
        if (productItem.inventory < product.quantity) {
          showMsg = true;
          product.quantity = productItem.inventory;
        }
        commit('changeQuantity', {
          isbn: product.isbn,
          quantity: product.quantity,
        });
        console.log("productItem.inventory", productItem.inventory, product.isbn, cartItem.quantity);
        //resolve("ok");
        if (showMsg) {
          reject("超出庫存數量");
        }

      } else {
        reject("購物車內無此商品");
      }
    });
  },
  addProductToCart({
    commit,
    state
  }, product) {
    return new Promise((resolve, reject) => {
      commit("refresh");
      const cartItem = state.items.find(item => item.isbn === product.isbn);
      if (!cartItem) {
        commit('pushProductToCart', {
          isbn: product.isbn,
          name: product.title,
          src: product.img,
          listPrice: product.listPrice,
          discount: product.discount
        });
        resolve("已放入購物車");
      } else {
        console.log("it already exists in cart", product.title, product.isbn);
        reject("商品已在購物車");
      }
    })
  },
  deleteItem({
    commit,
    state
  }, product) {
    commit("refresh");
    commit("deleteItem", {
      index: product.index,
      isbn: product.isbn,
    });
  },
  deleteSelected({
    commit,
  }, product) {
    commit("refresh");
    commit("deleteSelected");
  },
  clearState({
    commit,
  }) {
    commit("clearState");
  }
}

// mutations
const mutations = {
  pushProductToCart(state, {
    isbn,
    name,
    src,
    listPrice,
    discount
  }) {
    state.items.push({
      isbn,
      name,
      src,
      listPrice,
      discount,
      quantity: 1,
      checked: false,
    });
    localStorage.setItem("demo.cart.items", JSON.stringify(state.items));
  },
  refresh(state) {
    console.log("cart.js refresh");
    state.items = JSON.parse(localStorage.getItem("demo.cart.items")) || [];
  },
  clearState(state) {
    state.items = [];
    localStorage.removeItem("demo.cart.items");
  },
  incrementItemQuantity(state, {
    isbn
  }) {
    const cartItem = state.items.find(item => item.isbn === isbn)
    cartItem.quantity++
  },
  deleteItem(state, {
    isbn,
  }) {
    let index = state.items.findIndex(item => item.isbn === isbn)
    console.log("deleteItem: isbn", isbn, "index", index);
    if (index != -1) {
      state.items.splice(index, 1);
      localStorage.setItem("demo.cart.items", JSON.stringify(state.items));
    }
  },
  deleteSelected(state) {
    for (let i = state.items.length - 1; i >= 0; i--) {
      if (state.items[i].checked == true) {
        state.items.splice(i, 1);
      }
    }
    localStorage.setItem("demo.cart.items", JSON.stringify(state.items));
  },
  toggleAll(state, checked) {
    state.items.forEach(function (item) {
      item.checked = checked;
    });
    localStorage.setItem("demo.cart.items", JSON.stringify(state.items));
  },
  setChecked(state, {
    isbn,
    checked
  }) {
    let index = state.items.findIndex(item => {
      return item.isbn == isbn;
    });
    if (index != -1) {
      state.items[index].checked = checked;
      localStorage.setItem("demo.cart.items", JSON.stringify(state.items));
    }
    console.log(isbn, checked, index);
  },
  changeQuantity(state, {
    isbn,
    quantity
  }) {
    let index = state.items.findIndex(item => {
      return item.isbn == isbn;
    });
    if (index != -1) {
      state.items[index].quantity = quantity;
      localStorage.setItem("demo.cart.items", JSON.stringify(state.items));
    }
  },
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}