// initial state
const state = () => ({
  show: false,
  msg: "",
})

// getters
const getters = {
  modalShow(state) {
    console.log("modal.js modalShow", state.show);
    return state.show;
  },
  modalMsg(state) {
    console.log("modal.js modalMsg", state.msg);
    return state.msg;
  },
}

// mutations
const mutations = {
  refresh(state) {
    console.log("modal.js refresh");
  },
  setModalShow(state, show) {
    state.show = show;
  },
  setModalMsg(state, msg) {
    state.msg = msg;
  },
}

// actions
const actions = {
  refresh({
    commit
  }) {
    commit("refresh");
  },
  setModalShow({
    commit,
  }, show) {
    commit("setModalShow", show);
  },
  setModalMsg({
    commit
  }, msg) {
    commit("setModalMsg", msg);
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}