import Vue from "vue";
import VueRouter from "vue-router";
import store from '@/store';

Vue.use(VueRouter);

let router = new VueRouter({
  mode: 'history',
  base: __dirname,
  routes: [{
      path: "/",
      component: () => import("@/views/Root"),
      children: [{
          path: "",
          name: "index",
          component: () => import("@/views/Index")
        },
        {
          path: "login",
          name: "login",
          component: () => import("@/views/Login"),
          // https://router.vuejs.org/guide/advanced/navigation-guards.html#per-route-guard
          // https://router.vuejs.org/zh/guide/advanced/navigation-guards.html#%E8%B7%AF%E7%94%B1%E7%8B%AC%E4%BA%AB%E7%9A%84%E5%AE%88%E5%8D%AB
          beforeEnter: (to, from, next) => {
            if (store.getters['auth/isAuthenticated']) {
              store.dispatch("modal/setModalMsg", "已登入");
              store.dispatch("modal/setModalShow", true);
              next({
                name: "index"
              });
            } else {
              next();
            }
          }
        },
        {
          path: "product/:isbn",
          name: "product.isbn",
          props: true,
          component: () => import("@/views/Product"),
          beforeEnter: (to, from, next) => {
            console.log("beforeEnter: ", to.name, to.params.isbn);
            next();
          }
        },
        {
          path: "book/:isbn",
          name: "book.isbn",
          props: true,
          component: () => import("@/views/Book"),
          beforeEnter: (to, from, next) => {
            console.log("beforeEnter: ", to.name, to.params.isbn);
            next();
          }
        },
        {
          path: "shoppingcart",
          name: "shoppingcart",
          meta: {
            requiresAuth: true
          },
          component: () => import("@/views/Shoppingcart")
        },
        {
          path: "deliver",
          name: "deliver",
          meta: {
            requiresAuth: true,
            requiresProduct: true,
          },
          component: () => import("@/views/Deliver")
        },
        {
          path: "confirm",
          name: "confirm",
          meta: {
            requiresAuth: true,
            requiresProduct: true,
            requiresUserInfo: true
          },
          component: () => import("@/views/Confirm")
        },
        {
          path: "complete",
          name: "complete",
          meta: {
            requiresAuth: true,
          },
          component: () => import("@/views/Complete"),
          beforeEnter: (to, from, next) => {
            if (store.getters['complete/isCompleted']) {
              next();
            } else {
              store.dispatch("modal/setModalMsg", "訂單尚未成立");
              store.dispatch("modal/setModalShow", true);
              next({
                name: "index"
              });
            }
          }
        },
        {
          path: "order/:orderNo",
          name: "order.orderNo",
          meta: {
            requiresAuth: true,
          },
          component: () => import("@/views/Order"),
          beforeEnter: (to, from, next) => {
            console.log("beforeEnter: ", to.name, to.params.orderNo);
            next();
          }
        },
        {
          path: "query",
          name: "query",
          meta: {
            requiresAuth: true,
          },
          component: () => import("@/views/QueryOrder")
        },
        {
          path: "404",
          name: "404",
          component: () => import("@/views/PageNotFound")
        },
        {
          path: "templateRootEmpty",
          name: "templateRootEmpty",
          component: () => import("@/views/TemplateRootEmpty")
        },
      ]
    },
    {
      path: "/demo",
      name: "demo",
      component: () => import("@/Demo")
    },
    {
      path: "/hello",
      name: "hello",
      component: () => import("@/App")
    },
    {
      path: "/mylogin",
      name: "mylogin",
      component: () => import("@/views/MyLogin")
    },
    {
      path: "/meal",
      name: "meal",
      component: () => import("@/views/Meal")
    },
    {
      path: "/imageslider",
      name: "imageslider",
      component: () => import("@/views/ImageSlider")
    },
    {
      path: "/imageSliderSecond",
      name: "imageSliderSecond",
      component: () => import("@/views/ImageSliderSecond")
    },
    {
      path: "/imageSliderThird",
      name: "imageSliderThird",
      component: () => import("@/views/ImageSliderThird")
    },
    {
      path: "/myproduct/:isbn",
      name: "myproduct.isbn",
      props: true,
      component: () => import("@/views/MyProduct"),
      beforeEnter: (to, from, next) => {
        console.log("beforeEnter: ", to.name, to.params.isbn);
        next();
      }
    },
    {
      path: "/productPreliminary",
      name: "productPreliminary",
      component: () => import("@/views/ProductPreliminary")
    },
    {
      path: "/myshoppingcart",
      name: "myshoppingcart",
      component: () => import("@/views/MyShoppingcart")
    },
    {
      path: "/mydeliver",
      name: "mydeliver",
      component: () => import("@/views/MyDeliver")
    },
    {
      path: "/myconfirm",
      name: "myconfirm",
      component: () => import("@/views/MyConfirm")
    },
    {
      path: "/templateEmpty",
      name: "templateEmpty",
      component: () => import("@/views/TemplateEmpty")
    },
    {
      path: "/dynamicImport",
      name: "dynamicImport",
      component: () => import("@/views/DynamicImport")
    },
    {
      path: "/home",
      name: "home",
      component: () => import("@/views/Home")
    },
    {
      path: "/homePreliminary",
      name: "homePreliminary",
      component: () => import("@/views/HomePreliminary")
    },
    {
      path: "/homeSecond",
      name: "homeSecond",
      component: () => import("@/views/HomeSecond")
    },
    {
      path: "/my404",
      name: "my404",
      component: () => import("@/views/MyPageNotFound")
    },
  ]
});

router.beforeEach((to, from, next) => {
  // https://www.shade.codes/scroll-to-top-on-route-change-vue-js/
  window.scrollTo(0, 0);
  // https://stackoverflow.com/questions/45619407/how-to-create-a-404-component-in-vuejs-using-vue-router/51233853
  // https://jsfiddle.net/6cL7h4at/
  if (!to.matched.length) {
    next('/404');
  } else {
    store.dispatch('products/refresh');
    store.dispatch('cart/refresh');
    store.dispatch('auth/refresh');
    store.dispatch('deliver/refresh');
    store.dispatch('complete/refresh');

    // https://router.vuejs.org/zh/guide/advanced/meta.html
    if (to.matched.some(record => record.meta.requiresAuth)) {
      // this route requires auth, check if logged in
      // if not, redirect to login page.
      if (!store.getters['auth/isAuthenticated']) {
        next({
          path: '/login',
          query: {
            redirect: to.fullPath
          },
        });
      } else {
        if (to.matched.some(record => record.meta.requiresProduct)) {
          console.log("requiresProduct");
          if (store.getters['cart/cartProducts'].length == 0) {
            store.dispatch("modal/setModalMsg", "購物車無商品, 請先選購商品");
            store.dispatch("modal/setModalShow", true);
            next({
              name: "index"
            });
          } else {
            next();
          }
        } else if (to.matched.some(record => record.meta.requiresUserInfo)) {
          console.log("requiresUserInfo", store.getters['auth/getBuyer']);
          console.log("Object.keys(store.getters['auth/getBuyer']).length", Object.keys(store.getters['auth/getBuyer']).length);
          if (Object.keys(store.getters['auth/getBuyer']).length == 0) {
            store.dispatch("modal/setModalMsg", "請先輸入付款方式及寄送資訊");
            store.dispatch("modal/setModalShow", true);
            console.log("next deliver");
            next({
              name: "deliver"
            });
          } else {
            next();
          }
        } else {
          next();
        }
      }
    } else {
      next(); // 确保一定要调用 next()
    }
  }
});

export default router;

