// initial state
const state = {
  deliver: "postalDelivery",
  payment: "COD",
  invoiceType: "paper",
  receiverInfo: "same",
  zipcode: "200",
  county: "",
  district: "",
  receiverName: "",
  receiverNameState: null,
  receiverArea: "",
  receiverAreaState: null,
  receiverPhone: "",
  receiverPhoneState: null,
  receiverExt: "",
  receiverExtState: null,
  receiverMobile: "",
  receiverMobileState: null,
  receiverAddress: "",
  receiverAddressState: null,
};

// https://bootstrap-vue.org/docs/components/form-input#comp-ref-b-form-input-props
// state
// Boolean	null	Controls the validation state appearance of the component. `true` for valid, `false` for invalid, or `null` for no validation state

const defaultState = {
  deliver: "postalDelivery",
  payment: "COD",
  invoiceType: "paper",
  receiverInfo: "same",
  zipcode: "200",
  county: "",
  district: "",
  receiverName: "",
  receiverNameState: null,
  receiverArea: "",
  receiverAreaState: null,
  receiverPhone: "",
  receiverPhoneState: null,
  receiverExt: "",
  receiverExtState: null,
  receiverMobile: "",
  receiverMobileState: null,
  receiverAddress: "",
  receiverAddressState: null,
};

// getters
const getters = {
  getState(state) {
    console.log("deliver.js getState", JSON.stringify(state));
    return state;
  }
};

// mutations
const mutations = {
  refresh(state) {
    console.log("deliver refresh", JSON.parse(localStorage.getItem("demo.deliver.state")));

    let obj = JSON.parse(localStorage.getItem("demo.deliver.state")) || defaultState;
    if (obj) {
      for (let key of Object.keys(obj)) {
        state[key] = obj[key];
      }
    }

    console.log("deliver after refresh - state", JSON.stringify(state));
  },
  setState(state) {
    // for (let key of Object.keys(state)) {
    //   state[key] = obj[key];
    // }
    localStorage.setItem("demo.deliver.state", JSON.stringify(state));
  },
  setProperty(state, obj) {
    state[obj.key] = obj.value;
    // localStorage.setItem("demo.deliver.state", JSON.stringify(state));
  },
  clearState(state) {
    localStorage.removeItem("demo.deliver.state");
  },
};

// actions
const actions = {
  refresh({
    commit
  }) {
    commit("refresh");
  },
  clearState({
    commit,
  }) {
    commit("clearState");
  },
  setState({
    commit
  }) {
    commit("setState");
  },
  setProperty({
    commit
  }, property) {
    commit("setProperty", property);
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};