// initial state
const state = {
  orderItems: [],
  QRCode: "",
  orderNo: "",
};

// getters
const getters = {
  isCompleted(state) {
    console.log("complete.js isCompleted", state.orderItems.length);
    return !!state.orderItems.length ? true : false;
  },
  getOrderNo(state) {
    console.log("complete.js getOrderNo", state.orderNo);
    return state.orderNo;
  },
  getOrderItems(state) {
    console.log("complete.js getOrderItems", JSON.stringify(state.orderItems));
    return state.orderItems;
  },
  getQRCode(state) {
    console.log("complete.js QRCode", state.QRCode);
    return state.QRCode;
  },
};

// mutations
const mutations = {
  refresh(state) {
    console.log("complete.js refresh");
    state.orderItems = JSON.parse(localStorage.getItem("demo.complete.orderItems")) || [];
    state.QRCode = localStorage.getItem("demo.complete.QRCode") || "";
    state.orderNo = localStorage.getItem("demo.complete.orderNo") || "";
  },
  setOrderItems(state, items) {
    state.orderItems = JSON.parse(JSON.stringify(items));
    localStorage.setItem("demo.complete.orderItems", JSON.stringify(items));
  },
  setQRCode(state, code) {
    state.QRCode = code;
    localStorage.setItem("demo.complete.QRCode", code);
  },
  setOrderNo(state, orderNo) {
    state.orderNo = orderNo;
    localStorage.setItem("demo.complete.orderNo", orderNo);
  },
  clearState(state) {
    state.orderItems.splice(0, state.orderItems.length);
    state.QRCode = "";
    state.orderNo = "";
    localStorage.removeItem("demo.complete.orderItems");
    localStorage.removeItem("demo.complete.QRCode");
    localStorage.removeItem("demo.complete.orderNo");
  }
};

// actions
const actions = {
  refresh({
    commit
  }) {
    commit("refresh");
  },
  setOrderItems({
    commit,
  }, items) {
    commit("setOrderItems", items);
  },
  setQRCode({
    commit
  }, code) {
    commit("setQRCode", code);
  },
  setOrderNo({
    commit
  }, orderNo) {
    commit("setOrderNo", orderNo);
  },
  clearState({
    commit,
  }) {
    commit("clearState");
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};